<template>
  <div>
    <div class="content">
      <h1>Des blagues !</h1>
      <div class="jokes">
        <div>
          <h2>Modération</h2>
          <ul>
            <li v-for="joke in getToModerateJokes()" :key="joke.id">
              <span>{{ joke.title }} - {{ joke.content }}</span>
              <button class="btn_validate" @click="validateJoke(joke.id)" type="click">Valider</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "JokePage",
  data() {
    return {
      jokes: [],
    };
  },
  
  methods: {
    getToModerateJokes() {
      return this.jokes;
    },
    
    // envoie la blague à la liste de blagues lorsqu'elle est envoyée
    validateJoke(componentId) {
      fetch("https://api.telefun.eu.org/api/joke?joke_id="+componentId, {
        method: "PUT",
        dataType: 'json',
        headers: {
          'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
          'content-type': 'application/json'
        },
      });
      alert("La blague a été validée !")
      return false;
    },

    // envoie la blague à la waiting list
    submitBlague() {
      console.log('APPEL')
      fetch("https://api.telefun.eu.org/api/waiting_list", {
        method: "POST",
        body: JSON.stringify({
          "joke_id": document.getElementById('add_joke_waiting_list').value
        }),
        dataType: 'json',
        headers: {
          'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
          'content-type': 'application/json'
        },
      });
      return false;
    }
  },

  created() {
    fetch("https://api.telefun.eu.org/api/joke/toValidate", {
      method: "GET",
      headers: {
        'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
      },
    })
      .then((response) => response.json())
      .then((data) => {
        this.jokes = data;
      });
  },
};
</script>

<style type="scss">
.jokes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.jokes>div {
  width: 50%;
  min-width: 300px;
}

.jokes > li{
  line-height: normal;
}

.flex_container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: normal;
  align-items: normal;
  align-content: normal;
}

.flex_items:nth-child(1) {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
  margin-bottom: 5%;
}

.flex_items:nth-child(2) {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
}

.item {
  width: 16em;
}

.placeHolder {
  color: black;
  background-color: white;
}

.p {
  text-align: center;
  font-size: 1.5em;
}

.btn_validate{
  border-radius: 5px;
  padding: 5px;
  margin-left: 20px;
}
</style>

