<template>
  <div>
    <nav class="navbar navbar-toggleable-md navbar-light bg-faded">
      <div class="" id="navbarNav">
        <div class="navbar-home">
          <router-link to="/" class="nav-link">TéléFun</router-link>
        </div>
        <ul class="navbar-nav">
          <li class="nav-item" v-for="r in routes" :key="r.path">
            <router-link :to="r.path" class="nav-link">{{ r.name }}</router-link>
          </li>
        </ul>
      </div>
    </nav>
    <router-view class="view"></router-view>
  </div>
</template>

<script>
import { RouterLink, RouterView } from "vue-router";

import { useCookies } from "vue3-cookies";

const { cookies } = useCookies();


let routes;

if (
  cookies.get("bearer_token") == null
) {
  routes = [
    {
      name: "Contact",
      path: "/contact",
    },
    {
      name: "A propos",
      path: "/about",
    },
    {
      name: "Se connecter",
      path: "/login",
    }
  ];
} else {
  routes = [
    {
      name: "Blagues",
      path: "/joke",
    },
    {
      name: "Liste d'attente",
      path: "/waiting_list",
    },
    {
      name: "Configuration",
      path: "/admin",
    },
    {
      name: "Contact",
      path: "/contact",
    },
    {
      name: "A propos",
      path: "/about",
    },
    {
      name: "Se déconnecter",
      path: "/logout",
    }
  ];
}

export default {
  data: () => ({
    routes,
  }),
  name: "App",
  components: {
    RouterLink,
    RouterView,
  }
};
</script>

