<template>
  <div>
    <div class="content">
      <h1>Redirection vers le SSO de Rezel</h1>
      <p>
        Si vous n'êtes pas redirigé, c'est que vous n'êtes pas sur le réseau
        Campus-Télécom ou connecté au VPN.
      </p>
      <p>Il nous est donc impossible de vous identifier.</p>
    </div>
  </div>
</template>

<script>
function checkLAN() {
  fetch("https://api.telefun.eu.org/api/auth/state", {
    method: "GET",
    credentials: "include",
  })
    .then(() => {
      window.location.href =
        "https://api.telefun.eu.org/api/auth/login?callbackUrl=https://telefun.eu.org/";
    })
    .catch(() => {
      setTimeout(() => {
        checkLAN();
      }, 5000);
    });
}

export default {
  name: "LoginPage",
  beforeMount() {
    checkLAN();
  },
};
</script>

<style>
.content {
  margin-top: 10em;
}
</style>
