<template>
  <div>
    <div class="content">
      <h1>Des blagues !</h1>
      <div class="jokes">
        <div>
          <h2>Mes blagues</h2>
          <ul>
            <li v-for="joke in getJokes()" :key="joke.id">
              {{ joke.title }} - {{ joke.content }}
            </li>
          </ul>
        </div>
        <div>
          <h2>Ajouter une nouvelle blague</h2>
          <div class="containerGlobal">
            <form name="myForm" :onsubmit="submitForm" method="post" id="add_joke" disabled="disabled">
              <div class="containerB">
                <div class="item">
                  <div class="flex_container">
                    <div class="flex_items">
                      <input type="text" name="title" id="title" class="placeHolder" placeholder="Titre" />
                    </div>
                    <div class="flex_items">
                      <input type="text" name="content" id="content" class="placeHolder" placeholder="Blague" />
                    </div>
                  </div>
                </div>
                <button class="btn" type="submit">Ajouter</button>
              </div>
            </form>
              <div>
                <div>
                  <p>Sélectionnez une blague à insérer en fin de file d'attente :</p>
                  <div class="containerB">
                    <div class="flex_container">
                        <select name="myBlague"  method="post" id="add_joke_waiting_list">
                          <option :value="joke.id" v-for="joke in getJokes()" :key="joke.id" >
                            {{ joke.title }}
                          </option>
                        </select>
                        <button class="btn1" :onclick="submitBlague" type="click">Ajouter</button>
                    </div>
                  </div>
                </div>
              </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "JokePage",
  data() {
    return {
      jokes: [],
    };
  },
  
  methods: {
    getJokes() {
      return this.jokes;
    },
    
    // envoie la blague à la liste de blagues lorsqu'elle est envoyée
    submitForm() {
      fetch("https://api.telefun.eu.org/api/joke", {
        method: "POST",
        body: JSON.stringify({
          "title": document.getElementById('title').value,
          "content": document.getElementById('content').value
        }),
        dataType: 'json',
        headers: {
          'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
          'content-type': 'application/json'
        },
      });
      alert("La blague a bien été ajoutée à la base de données ! Elle est en attente de modération.")
      return false;
    },

    // envoie la blague à la waiting list
    submitBlague() {
      console.log('APPEL')
      fetch("https://api.telefun.eu.org/api/waiting_list", {
        method: "POST",
        body: JSON.stringify({
          "joke_id": document.getElementById('add_joke_waiting_list').value
        }),
        dataType: 'json',
        headers: {
          'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
          'content-type': 'application/json'
        },
      });
      alert("La blague a bien été ajoutée à la waiting list !")
      return false;
    }
  },

  created() {
    fetch("https://api.telefun.eu.org/api/joke", {
      method: "GET",
      headers: {
        'Authorization': "Bearer " + this.$cookies.get("bearer_token"),
      },
    })
      .then((response) => response.json())
      .then((data) => {
        this.jokes = data;
      });
  },
};
</script>

<style type="scss">
.jokes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.jokes>div {
  width: 50%;
  min-width: 300px;
}

.flex_container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: normal;
  align-items: normal;
  align-content: normal;
}

.flex_items:nth-child(1) {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
  margin-bottom: 5%;
}

.flex_items:nth-child(2) {
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
}
.containerB {
  margin-top: 5%;
  margin-bottom: 5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.containerGlobal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item {
  width: 16em;
}

.placeHolder {
  color: black;
  background-color: white;
}

.btn {
  display: inline-block;
  background: white;
  border-radius: 50px;
  font-size: 200;
  color: black;
  margin-top: 10px;
  font-weight: 500;
}
.btn1 {
  display: inline-block;
  background: white;
  border-radius: 50px;
  font-size: 200;
  color: black;
  font-weight: 500;
  margin-top: 15%;
}
.p {
  text-align: center;
  font-size: 1.5em;
}
</style>

