<template>
  <div>
    <div class="flex-waiting content">
      <div class="waiting-list">
        <h1>Waiting List</h1>
        <p>
          Vous pouvez voir ici la liste des blagues en attente, quand votre
          blague sera en première place ce sera la prochaine à être exécuté:
        </p>
        <h2>
          <li v-for="joke in getWaitingList()" :key="joke.id">
            {{joke.joke.title}} - {{ joke.joke.content }}
          </li>
        </h2>
      </div>
    </div>
  </div>
</template>
 
<script>
export default {
  name: "WaitingListPage",
  data() {
    return {
      waitingList: [],
      jokes: [],
    };
  },
  methods: {
    getWaitingList() {
      return this.waitingList;
    },
    getJokes() {
      return this.jokes;
    },
  },
  created() {
    fetch("https://api.telefun.eu.org/api/waiting_list", {
      method: "GET",
      headers: {
        Authorization: "Bearer " + this.$cookies.get("bearer_token"),
      },
    })
      .then((response) => response.json())
      .then((data) => {
        this.waitingList = data;
      });
  },
};
</script>
 
<style type="scss">
.flex-waiting {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  text-shadow: 0 0 1px #000;
}
.flex-waiting > div {
  width: 60%;
  min-width: 300px;
  text-align: center;
  text-rendering: optimizeLegibility;
}
.waiting-list h2 {
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 2rem;
  
}
.waiting-list p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  text-align: center;
  color: whitesmoke;
}
.waiting-list h1{
  font-size: 2rem;
  font-weight: 400;
  text-align: center;
  color: red ;
}
</style>