<template>
  <div class="logout-page">
    <h1>Vous avez été déconnecté</h1>
    <p>Vous allez être redirigé vers la page d'accueil</p>
  </div>
</template>

<script>
import { useCookies } from "vue3-cookies";


export default {
  name: "LogoutPage",
  beforeMount() {
    fetch("https://api.telefun.eu.org/api/auth/logout", {
      method: "GET",
      headers: {
        Authorization: "Bearer " + this.$cookies.get("bearer_token"),
      },
    });

    // On redirige vers la page d'accueil
    setTimeout(() => {
      window.location.href = "/";
    }, 500);
    const { cookies } = useCookies();
    cookies.remove("email", "/", ".telefun.eu.org");
    cookies.remove("name", "/", ".telefun.eu.org");
    cookies.remove("bearer_token", "/", ".telefun.eu.org");
  },
};
</script>
