import * as VueRouter from "vue-router";
import Home from "../pages/HomePage.vue";
import About from "../pages/AboutPage.vue";
import Logout from "../pages/LogoutPage.vue";
import LoginPage from "../pages/LoginPage.vue";
import JokePage from "../pages/JokePage.vue";
import WaitingListPage from "../pages/WaitingListPage.vue";
import Contact from "../pages/ContactPage.vue";
import Admin from "../pages/AdminPage.vue";

let r = VueRouter.createRouter({
  history: VueRouter.createWebHistory(),
  routes: [
    {
      path: "/",
      component: Home,
    },
    {
      path: "/waiting_list",
      component: WaitingListPage,
    },
    {
      path: "/joke",
      component: JokePage,
    },
    {
      path: "/about",
      component: About,
    },
    {
      path: "/admin",
      component: Admin,
    },
    {
      path: "/contact",
      component: Contact,
    },
    {
      path: "/logout",
      component: Logout,
    },
    {
      path: "/login",
      component: LoginPage
    },
    {
      path: "/:pathMatch(.*)*",
      redirect: "/"
    }
  ],
});

export default r;
